@import "utils";
@import "fonts";
@import "typography";

body {
  background: $c-gray;
}

.calendar {
  display: grid;
  grid-template-columns: repeat(7, 100px);
  grid-template-rows: repeat(5, 100px);
  grid-column-gap: 1px;
  grid-row-gap: 1px;
  div {
    display: grid;
    border: 1px solid black;
    justify-items: center;
    align-items: center;
    position: relative;
    bottom: 0;
    transition: all 0.5s linear;
    &:hover {
      background-color: #ffbfcb;
      cursor: pointer;
      bottom: 20px;
      transform: translate(0, -20px);
      }
    .cell {
      font-size: 30px;
      font-family: Arial, sans-serif;
      font-weight: normal;
      & .cell-1 {
        grid-column: 2;
      }
    }
  }
}

.calendar-28 {
  .cell:nth-child(29), .cell:nth-child(30) ,.cell:nth-child(31) {
    display: none;
  }
}


.calendar-29 {
  .cell:nth-child(30), .cell:nth-child(31) {
    display: none;
  }
}
